@import url(https://cdnjs.cloudflare.com/ajax/libs/animate.css/3.7.0/animate.min.css);
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

*:focus {
    outline:none
}

li {
    list-style: none
}

a {
    color: #2c3e50;
}

a:hover {
    text-decoration: none;
    color: #2c3e50;
}

.search-input {
    width: 100%;
    padding: 15px;
    border: none;
    -webkit-box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.1);
    -webkit-appearance: none;
    z-index: 100;
    border-radius: 4px;
}

#movies {
    text-align: left;
}

.result-container {
    border-radius: 4px;
    -webkit-box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
            box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05);
    -webkit-appearance: none;
    max-height: 318px;
    height: 318px;
    overflow: hidden
}

tbody, tr, table, .table {
    border:none !important;
}

button {
    background: none;
    border: none;
    cursor: pointer;
}

.list-button i {
    color: rgba(0, 0, 0, 0.1);
    -webkit-transition: color 0.2s ease;
    transition: color 0.2s ease;
}

.list-button i:hover {
    color: rgba(0, 0, 0, 0.3);
}

.delete-button {
    color: rgba(158, 158, 158, 0.3);
    -webkit-transition: color 0.5s ease;
    transition: color 0.5s ease;
}

.delete-button:hover {
    color: rgba(74, 71, 157, 1);
}

#nprogress .bar {
    background: #4A479D;
    height: 3px;
}

#nprogress .spinner {
    display: none;
}

.bold {
    font-weight: bold
}

.table-responsive {
    margin-top: 30px;
    margin-bottom: 30px;
}

.back-button{
    margin-bottom: 30px
}

.back-button span {
    font-size: 19px;
    font-weight: bolder;
    color: rgba(34, 196, 169, 1);
}

.back-button i {
    margin-top: -6px;
    color: rgba(34, 196, 169, 1);
}

.list-button {
    position: absolute;
    top: 30px;
    right: 30px
}

.list-button i {
    font-size: 40px
}

.popular-head-title {
    font-weight: bold;
    margin-bottom: 30px;
    margin-left: 10px
}

.popular {
    margin-top: 100px
}

.add-button {
    color: rgba(34, 196, 169, 0.3);
    -webkit-transition: color 0.5s ease;
    transition: color 0.5s ease;
}

.add-button:hover {
    color: rgba(34, 196, 169, 1);
}

.popular-result {
    -webkit-transition: -webkit-transform 0.2s ease;
    transition: -webkit-transform 0.2s ease;
    transition: transform 0.2s ease;
    transition: transform 0.2s ease, -webkit-transform 0.2s ease;
    -webkit-transform: scale(1);
            transform: scale(1)
}

.popular-result:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1)
}